/* COMMON styles */
@font-face {
  font-family: 'robotoregular';
  src: url("../fonts/roboto-regular-webfont.eot");
  src: url("../fonts/roboto-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/roboto-regular-webfont.woff") format("woff"), url("../fonts/roboto-regular-webfont.ttf") format("truetype"), url("../fonts/roboto-regular-webfont.svg#robotoregular") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'robotobold';
  src: url("../fonts/roboto-bold-webfont.eot");
  src: url("../fonts/roboto-bold-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/roboto-bold-webfont.woff") format("woff"), url("../fonts/roboto-bold-webfont.ttf") format("truetype"), url("../fonts/roboto-bold-webfont.svg#robotobold") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto-Medium';
  src: url("../fonts/Roboto-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Medium.woff") format("woff"), url("../fonts/Roboto-Medium.ttf") format("truetype"), url("../fonts/Roboto-Medium.svg#Roboto-Medium") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto-Light';
  src: url("../fonts/Roboto-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Light.woff") format("woff"), url("../fonts/Roboto-Light.ttf") format("truetype"), url("../fonts/Roboto-Light.svg#Roboto-Light") format("svg");
  font-weight: normal;
  font-style: normal;
}

html,
body {
  background: #ffffff;
  color: #585858;
  font-size: 16px;
  line-height: 1.4;
  font-family: 'robotoregular', sans-serif;
  font-weight: normal;
}

a, .as-link {
  color: #5A7CE5;
  text-decoration: none;
  transition: all 0.3s ease;
}

a:hover, .as-link:hover {
  color: #32b9fc;
}

a:focus, .as-link:focus {
  outline: none;
}

.as-link {
  cursor: pointer;
}

b, strong {
  font-family: 'robotobold', sans-serif;
  font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-family: 'robotobold', sans-serif;
}

h1 {
  margin-top: 0;
  padding-top: 0;
  font-size: 33px;
}

h2 {
  margin: 0 0 55px;
  padding: 0;
  color: #3C3C3C;
  font-size: 30px;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 20px;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
}

.text-bold {
  font-family: 'robotobold', sans-serif;
  font-weight: normal;
}

.text-nowrap {
  white-space: nowrap;
}

.text-lowercase {
  text-transform: lowercase;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-capitalize {
  text-transform: capitalize;
}

.pull-left {
  float: left;
}

.pull-right {
  float: right;
}

.list-unstyled {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.list-blue li {
  padding-left: 22px;
  font-size: 15px;
  letter-spacing: 1px;
  line-height: 27px;
  position: relative;
}

.list-blue li::before {
  width: 7px;
  height: 7px;
  margin-right: 12px;
  border-radius: 50%;
  background-color: #2FC0FF;
  content: '';
  position: absolute;
  left: 0;
  top: 10px;
}

.list-dash {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.list-dash li {
  font-size: 15px;
  letter-spacing: 1px;
  line-height: 27px;
  font-family: 'Roboto-Light', sans-serif;
}

.list-dash li::before {
  margin-right: 5px;
  display: inline;
  content: '-';
}

.clearfix:after {
  display: table;
  clear: both;
  content: "";
}

button:focus, button:active, input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active {
  outline: none;
  box-shadow: none;
}

.text-blue {
  color: #4580E0;
}

.wrapper {
  width: 100%;
  min-width: 768px;
  overflow: hidden;
}

.modal-dialog {
  width: 480px;
}

/* forms */
.hide-placeholder::-webkit-input-placeholder {
  opacity: 1;
  color: #939393;
  font-size: 16px;
  transition: opacity 0.3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.hide-placeholder::-moz-placeholder {
  opacity: 1;
  color: #939393;
  font-size: 16px;
  transition: opacity 0.3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.hide-placeholder:-moz-placeholder {
  opacity: 1;
  color: #939393;
  font-size: 16px;
  transition: opacity 0.3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.hide-placeholder:-ms-input-placeholder {
  opacity: 1;
  color: #939393;
  font-size: 16px;
  transition: opacity 0.3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.hide-placeholder:focus::-webkit-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus::-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus:-moz-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.hide-placeholder:focus:-ms-input-placeholder {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.form-group {
  margin-bottom: 15px;
}

.form-control {
  width: 100%;
  height: 52px;
  padding: 0 27px 2px;
  font-size: 16px;
  border-radius: 2px;
  color: #585858;
  border: 1px solid #E1E6E9;
  background-color: #ffffff;
  font-weight: normal;
  display: block;
  transition: all .3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.form-control:focus, .form-control:active {
  border-color: #2FC0FF;
}

textarea.form-control {
  height: 188px;
  padding: 19px 47px 10px;
  resize: none;
  overflow: hidden;
}

.form-error {
  position: relative;
  font-family: 'Roboto-Light', sans-serif;
}

.form-error .form-control {
  border-color: #FF8C8C;
}

.form-error-text {
  color: #FF3333;
  position: absolute;
  top: 16px;
  left: 27px;
  background-color: #fff;
  font-size: 16px;
  font-weight: normal;
}

.modal-message {
  padding: 15px 0;
  display: flex;
  align-items: center;
}

.modal-message-icon {
  width: 42px;
  height: 42px;
  padding-top: 12px;
  margin-right: 20px;
  border-radius: 50%;
  flex: 1 0 42px;
}

.modal-message-icon svg {
  width: 19px;
  height: 19px;
  fill: #fff;
}

.modal-message-text {
  margin-bottom: 0;
  letter-spacing: 0;
  text-align: left;
  font-size: 16px;
  font-family: 'Roboto-Light', sans-serif;
}

.error-block .modal-message-icon {
  background-color: #FF8C8C;
}

.error-block .modal-message-text {
  color: #FF3333;
}

.message-successful .modal-message-icon {
  background-color: #2FC0FF;
}

.message-successful .modal-message-text {
  color: #4580E0;
}

.radio-custom {
  display: none;
  transition: all 0.3s ease;
}

.radio-custom-view {
  padding-top: 6px;
  padding-left: 40px;
  display: inline-block;
  height: 25px;
  position: relative;
  cursor: pointer;
  font-size: 16px;
  color: #585858;
  font-family: 'Roboto-Light', sans-serif;
}

.radio-custom-view::before {
  background-color: #fff;
  border-radius: 50%;
  content: '';
  width: 23px;
  height: 23px;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 0 1px #E1E6E9;
  transition: all .3s ease;
}

.radio-custom-view::after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #E0A700;
  opacity: 0;
  visibility: hidden;
  transition: all .3s ease;
}

.radio-custom:checked + .radio-custom-view {
  color: #4580E0;
}

.radio-custom:checked + .radio-custom-view::before {
  box-shadow: 0 0 0 2px #2FC0FF;
}

.radio-custom:checked + .radio-custom-view::after {
  opacity: 1;
  visibility: visible;
}

.radio-custom:disabled + .radio-custom-view {
  color: #E1E6E9;
  cursor: not-allowed;
}

.radio-custom:disabled + .radio-custom-view::before {
  background-color: #E1E6E9;
}

.radio-wrap {
  margin-bottom: 0;
  font-size: 0;
}

.radio-wrap:not(:last-of-type) {
  margin-right: 31px;
}

/* end forms */
/* btn */
.btn {
  border-width: 0;
  background: transparent;
  text-align: center;
  cursor: pointer;
  line-height: 1;
  display: inline-block;
  text-transform: uppercase;
  position: relative;
  transition: all .3s ease;
  font-family: 'robotobold', sans-serif;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1;
  transition: all 0.3s ease;
}

.btn span {
  position: relative;
  z-index: 3;
}

.btn-transparent {
  min-width: 200px;
  height: 45px;
  padding: 14px 29px 0;
  color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 2px;
  font-size: 15px;
  letter-spacing: 1px;
  fill: #ffffff;
  align-items: center;
  white-space: nowrap;
  font-weight: normal;
  box-shadow: 0 8px 15px rgba(255, 255, 255, 0.15);
}

.btn-transparent svg {
  width: 14px;
  height: 13px;
  margin-left: 10px;
  fill: inherit;
}

.btn-transparent:hover, .btn-transparent:active {
  background-color: #ffffff;
  color: #4580E0;
  fill: #4580E0;
  box-shadow: 0 8px 15px 0 rgba(69, 128, 224, 0.25);
}

.btn-yellow {
  min-width: 202px;
  height: 60px;
  padding-top: 7px;
  color: #ffffff;
  border-radius: 2px;
  position: relative;
  letter-spacing: 1.17px;
  font-size: 17px;
  font-weight: normal;
  background-image: linear-gradient(-270deg, #E0A700 0%, #FFBE00 100%);
}

.btn-yellow:not(:hover) {
  box-shadow: 0 8px 15px 0 rgba(232, 173, 0, 0.35);
}

.btn-yellow span {
  top: -2px;
  align-self: center;
}

.btn-yellow::before {
  background: #E0A700;
}

.btn-yellow:hover, .btn-yellow:active {
  color: #ffffff;
}

.btn-yellow:hover::before, .btn-yellow:active::before {
  opacity: 1;
}

.btn-yellow:active {
  box-shadow: none;
}

.btn-online {
  min-width: 155px;
  height: 60px;
  padding-top: 23px;
  border-radius: 2px;
  font-size: 14px;
  text-transform: uppercase;
  color: #FFFFFF;
  letter-spacing: 1px;
  position: relative;
  background-image: linear-gradient(90deg, #4580E0 0%, #2FC1FF 100%);
  font-family: 'robotobold', sans-serif;
}

.btn-online:not(:hover) {
  box-shadow: 0 8px 15px 0 rgba(69, 128, 224, 0.35);
}

.btn-online span {
  top: -2px;
}

.btn-online .icon-vk {
  width: 24px;
  height: 14px;
  margin-left: 5px;
  fill: #ffffff;
  position: relative;
  z-index: 3;
}

.btn-online::before {
  background: #2FC1FF;
}

.btn-online:hover {
  color: #fff;
}

.btn-online:hover::before {
  opacity: 1;
}

.btn-blue {
  width: 200px;
  height: 80px;
  padding: 20px 34px;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 1.24px;
  background: #4580e0;
  background: -moz-linear-gradient(left, #4580e0 0%, #2fe9ff 100%);
  background: -webkit-linear-gradient(left, #4580e0 0%, #2fe9ff 100%);
  background: linear-gradient(to right, #4580e0 0%, #2fe9ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4580e0', endColorstr='#2fe9ff', GradientType=1);
  font-family: 'robotobold', sans-serif;
}

.btn-blue:not(:hover) {
  box-shadow: 0 8px 15px 0 rgba(69, 128, 224, 0.35);
}

.btn-blue::before {
  background: #2FC1FF;
}

.btn-blue:hover {
  color: #fff;
}

.btn-blue:hover::before {
  opacity: 1;
}

/* end btn */
.nav-tabs-blue {
  height: 55px;
  padding-left: 0;
  margin-bottom: 50px;
  background-color: #ffffff;
  list-style: none;
  font-size: 17px;
  display: flex;
}

.nav-tabs-blue li {
  padding-top: 17px;
  text-align: center;
  border: 1px solid transparent;
  border-right-color: #EBEEF0;
  letter-spacing: 1.1px;
  flex-basis: calc(100% / 3);
  transition: all 0.3s ease;
  font-family: 'Roboto-Medium', sans-serif;
}

.nav-tabs-blue li:not(.active) {
  border-top-color: #EBEEF0;
  border-bottom-color: #EBEEF0;
  cursor: pointer;
}

.nav-tabs-blue li:not(.active):first-child {
  border-left-color: #EBEEF0;
}

.nav-tabs-blue li:not(.active):hover {
  background-color: #2FC1FF;
  border-top-color: #2FC1FF;
  border-bottom-color: #2FC1FF;
}

.nav-tabs-blue li:hover, .nav-tabs-blue .active {
  color: #ffffff;
}

.nav-tabs-blue .active {
  background-image: linear-gradient(45deg, #4580E0 0%, #2FC1FF 100%);
}

/* END COMMON */
/* header */
.header {
  padding-top: 50px;
  height: 820px;
  color: #fff;
  position: relative;
  background: #4580e0;
  background: -moz-linear-gradient(-45deg, #4580e0 0%, #2fe9ff 100%);
  background: -webkit-linear-gradient(-45deg, #4580e0 0%, #2fe9ff 100%);
  background: linear-gradient(135deg, #4580e0 0%, #2fe9ff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4580e0', endColorstr='#2fe9ff', GradientType=1);
}

.header::after {
  width: 100%;
  height: 170px;
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  background: url(../img/bg-header.png) no-repeat top center;
}

.header a {
  color: inherit;
}

.top-panel {
  margin-bottom: 150px;
  display: flex;
}

.logo {
  padding-top: 8px;
  text-transform: uppercase;
  font-family: 'robotobold', sans-serif;
}

.site-name {
  font-size: 20px;
  display: inherit;
}

.from-author {
  font-size: 14px;
  color: #FEBD00;
  letter-spacing: 0.7px;
}

.menu {
  padding-top: 13px;
  flex-grow: 1;
  text-align: right;
  font-size: 0;
  text-transform: uppercase;
}

.menu li {
  font-size: 14px;
  display: inline-block;
  vertical-align: top;
}

.menu li:not(:first-child) {
  margin-left: 30px;
}

.contact-btn {
  width: 165px;
  height: 45px;
  line-height: 47px;
  margin-left: 40px;
  background-color: #4580E0;
  border-radius: 2px;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 0.96px;
  cursor: pointer;
  font-family: 'robotobold', sans-serif;
}

.contact-btn .icon-vk {
  width: 24px;
  height: 14px;
  margin-left: 5px;
  fill: #ffffff;
  top: 1px;
  position: relative;
}

.contact-btn:hover {
  background-color: #FFBE00;
  color: inherit;
}

.header-content {
  margin-bottom: 125px;
  display: flex;
  justify-content: space-between;
}

.header-content-title {
  margin-bottom: 27px;
  font-size: 25px;
  letter-spacing: 1.7px;
  text-transform: uppercase;
  font-weight: normal;
  display: flex;
  align-items: center;
  font-family: 'robotobold', sans-serif;
}

.header-content-title svg {
  width: 51px;
  height: 44px;
  margin-right: 27px;
}

.header-content-list {
  margin-bottom: 37px;
  letter-spacing: 1.1px;
  font-size: 0;
}

.header-content-list li {
  font-size: 16px;
}

.header-content-list li:nth-child(odd)::before {
  width: 8px;
  height: 8px;
  margin-top: 6px;
  margin-right: 10px;
  border-radius: 50%;
  background-color: #ffffff;
  content: '';
  display: inline-block;
  vertical-align: top;
  box-shadow: 2px 0 6px 0 rgba(77, 115, 254, 0.45);
}

.for-business {
  flex-basis: 466px;
}

.for-info {
  flex-basis: 406px;
}

.header-action {
  font-size: 17px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.header-action-string {
  line-height: 1;
  margin-right: 75px;
  color: #fff;
  position: relative;
  top: -3px;
  text-decoration: underline;
  font-family: 'Roboto-Medium', sans-serif;
}

.header-action-string:hover {
  text-decoration: none;
}

.header-full {
  height: 100vh;
  min-height: 740px;
}

.header-full .container {
  height: 100vh;
  min-height: 740px;
  padding-bottom: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header-full .top-panel, .header-full .header-content {
  margin-bottom: 0;
}

/* END header */
/* benefits */
.benefits-wrap {
  padding-top: 20px;
  position: relative;
}

.benefits-wrap::after {
  width: 100%;
  height: 400px;
  content: '';
  position: absolute;
  top: 100%;
  left: 0;
  background: url(../img/bg-for-business.png) no-repeat center top;
}

.benefits {
  list-style: none;
  padding-left: 0;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  justify-content: space-around;
}

.benefits [class^="icon-"] {
  width: 96px;
  height: 96px;
  margin: 0 auto 18px;
  background: #FFFFFF;
  border-radius: 50%;
  box-shadow: 0 8px 12px 0 rgba(191, 191, 191, 0.35);
  display: flex;
  align-items: center;
  justify-content: center;
}

.benefits .icon-experience svg {
  width: 50px;
  height: 50px;
}

.benefits .icon-any-complexity svg {
  width: 45px;
  height: 60px;
}

.benefits .icon-orders svg {
  width: 50px;
  height: 50px;
}

.benefits .icon-any-language svg {
  width: 53px;
  height: 50px;
}

.benefits li {
  padding: 0 15px;
}

.benefits-text {
  margin-bottom: 15px;
  display: inline-block;
}

/* END benefits */
/* tariff */
.tariff {
  padding-top: 150px;
  margin-bottom: 50px;
  position: relative;
}

.tariff::before {
  width: 100%;
  height: 170px;
  background: url(../img/bg-writing-texts.jpg) no-repeat center top;
  content: '';
  position: absolute;
  bottom: -167px;
  left: 0;
}

.tariff .nav-tabs li::before {
  width: 17px;
  height: 17px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-size: contain;
  content: '';
  display: inline-block;
  vertical-align: top;
  transition: all 0.3s ease;
}

.tariff .nav-tabs li:not(.active):first-child::before {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2217%22%20height%3D%2217%22%20viewBox%3D%220%200%2017%2017%22%20version%3D%221.1%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Ctitle%3Eicon%3C%2Ftitle%3E%3Cg%20stroke%3D%22none%22%20stroke-width%3D%221%22%20fill%3D%22none%22%20fill-rule%3D%22evenodd%22%3E%3Cpath%20fill%3D%22%23585858%22%20d%3D%22M.5%208H4.51v9H.5z%22%2F%3E%3Cpath%20stroke%3D%22%23585858%22%20d%3D%22M7.012%204.5h3.007v12H7.01z%22%2F%3E%3Cpath%20stroke%3D%22%23585858%22%20d%3D%22M13.022.5h3.007v16H13.02z%22%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E");
