}

.faq .collapsed:hover .arrow-cl {
  fill: #4580E0;
}

.faq-item:not(:last-of-type) {
  margin-bottom: 50px;
}

.faq-title {
  margin: 0;
  padding: 0 45px 25px;
  color: #4580E0;
  font-size: 16px;
  cursor: pointer;
  border-bottom: 1px solid #eaeaea;
  position: relative;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  transition: all 0.3s ease;
  font-family: 'robotoregular', sans-serif;
  font-weight: normal;
}

.faq-title:not(.collapsed) .arrow-cl {
  transform: rotateZ(180deg);
}

.faq-title::before {
  border-radius: 50%;
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  width: 25px;
  height: 25px;
  background: url("data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2212%22%20height%3D%2215%22%20viewBox%3D%220%200%2012%2015%22%20version%3D%221.1%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Ctitle%3ESVGCleanerId_0_1_%3C%2Ftitle%3E%3Cpath%20d%3D%22M7.257%2014.734H4.77V8.377h1.25c1.367%200%202.618-1.12%202.618-2.62%200-1.366-1.25-2.617-2.62-2.617-1.366%200-2.617%201.12-2.617%202.618H.914C.913%202.893%203.153.652%206.02.652c2.865%200%205.106%202.24%205.106%205.106%200%202.37-1.616%204.364-3.856%204.99v3.986h-.013z%22%20fill-rule%3D%22nonzero%22%20fill%3D%22%23FFF%22%20stroke%3D%22none%22%20stroke-width%3D%221%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #4580E0;
  background-size: 55%;
  transition: fill 0.3s ease;
}

.faq-title .arrow-cl {
  width: 14px;
  height: 9px;
  position: absolute;
  right: 28px;
  top: 5px;
  fill: #4580E0;
  transition: fill 0.3s ease;
}

.faq-body {
  padding: 13px 23px;
  line-height: 1.3;
  background: #FFFFFF;
  margin-top: 30px;
  font-size: 15px;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  font-family: 'Roboto-Light', sans-serif;
}

.faq-collapse .faq-body {
  box-shadow: 0 8px 12px 0 rgba(191, 191, 191, 0);
  opacity: 0;
}

.faq-collapse.in .faq-body {
  opacity: 1;
  box-shadow: 0 8px 12px 0 rgba(191, 191, 191, 0.35);
}

/* END faq */
/* cooperation */
