/* footer */
footer {
  padding-bottom: 20px;
  position: relative;
}

footer::before {
  width: 100%;
  height: 164px;
  margin-top: -160px;
  display: block;
  content: '';
  background: url(../img/bg-footer.png) no-repeat center top;
  position: relative;
  bottom: -41px;
}

footer .container {
  font-size: 15px;
  position: relative;
  z-index: 5;
