/* order-modal */
.order-modal .modal-title {
  padding: 20px 15px 13px;
  border-bottom: 1px solid #E1E6E9;
  position: relative;
}

.order-modal .close {
  position: absolute;
  top: 33px;
  right: 39px;
  float: none;
  opacity: 1;
  z-index: 9;
}

.order-modal .close:hover .icon-close {
  fill: #0c83e7;
}

.order-modal .icon-close {
  width: 19px;
  height: 19px;
  fill: #999999;
  transition: all 0.3s ease;
}

.order-modal .modal-body {
  padding: 26px 39px 35px;
}

.order-modal .nav-tabs {
  padding-left: 0;
  margin-bottom: 25px;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.order-modal .nav-tabs li {
  height: 55px;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
  font-size: 18px;
  text-transform: uppercase;
  padding-top: 13px;
  text-align: center;
  transition: color 0.3s ease;
  font-family: 'Roboto-Medium', sans-serif;
}

.order-modal .nav-tabs li:not(.active) {
  cursor: pointer;
}

.order-modal .nav-tabs li:not(.active):hover {
  color: #4580E0;
}

.order-modal .nav-tabs .active {
  padding-left: 33px;
  padding-right: 33px;
  color: #4580E0;
  border: 2px solid #2FC0FF;
  cursor: default;
}

.order-modal .tab-content {
  text-align: center;
}

.order-modal .form-group {
  text-align: left;
}

.order-modal .under-core {
  padding: 9px 0 9px;
  line-height: 1;
  display: flex;
}

.order-modal .under-core-label {
  margin-right: 32px;
  padding-top: 3px;
  font-weight: normal;
  font-family: 'robotoregular', sans-serif;
  font-weight: normal;
}

.order-modal .under-core-radio {
  flex-grow: 1;
  display: flex;
}

.order-modal .btn {
  margin-top: 17px;
}

.modal-open .wrapper {
  filter: blur(2px);
}

/* END order-modal */
.fancybox-view {
  cursor: pointer;
  position: relative;
  display: inline-block;
}

.fancybox-view::before, .fancybox-view::after {
  position: absolute;
  content: '';
  transition: all 0.3s ease;
}

.fancybox-view::before {
  width: 100%;
  height: 100%;
  background: rgba(181, 181, 181, 0.35);
  opacity: 0;
  z-index: 5;
}

.fancybox-view::after {
  width: 86px;
  height: 64px;
  bottom: 50%;
  left: 50%;
  margin: 0 0 -39px -41px;
  background: url(../img/icons/view.svg) no-repeat center;
  background-size: contain;
  opacity: 0;
  z-index: 6;
}

.fancybox-view img {
  transition: all 0.3s ease;
  box-shadow: 0 0 25px rgba(195, 195, 195, 0.45);
}

.fancybox-view:hover::before, .fancybox-view:hover::after {
  opacity: 1;
}

.fancybox-view:hover img {
  box-shadow: 0 0 0 rgba(195, 195, 195, 0.45);
}
